"use client"

import { useEffect, useState } from "react"
import axios from "axios"
import { toast } from "sonner"
import ActivityCard, { ActivityCardProps } from "@/components/admin-activity-card"
import foodImg from "@/../public/food2.png"

type ApiResponse = {
  success: boolean
  message: string[]
  donations: {
    _id: string
    mess: string
    volunteer: string
    // Optionally add image if backend sends it
  }[]
}

const Donations = () => {
  const [activityInfo, setActivityInfo] = useState<ActivityCardProps[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchDonations = async () => {
      try {
        const response = await axios.get<ApiResponse>("http://localhost:9000/admin/donations", {
          withCredentials: true,
        })

        if (response.data.success) {
          const mapped = response.data.donations.map(donation => ({
            ...donation,
            image: foodImg, // static image for now
          }))
          setActivityInfo(mapped)
        } else {
          toast.warning("Failed to fetch donations", {
            description: response.data.message.join(", "),
          })
        }
      } catch (err) {
        toast.error("Error fetching donations")
        console.error(err)
      } finally {
        setLoading(false)
      }
    }

    fetchDonations()
  }, [])

  return (
    <div className="w-full h-full px-4">
      <div className="w-full">
        <h1 className="text-center text-3xl font-bold mb-5">Donations</h1>
      </div>
      {
        loading ? (
          <p className="text-center">Loading...</p>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8 my-5 justify-items-center">
            {
              activityInfo.map((item) => (
                <ActivityCard {...item} key={item._id} />
              ))
            }
          </div>
        )
      }
    </div>
  )
}

export default Donations
